         if (this.formr.mobile===''&&this.formr.password===''&&this.formr.step==='') {
            this.$message.error('填写账号密码');
         } else {
            await getxiaomi(this.formr)
            const res = await getxiaomi(this.formr)
            console.log('res',res);
            if (res.data.code === 200) {
               this.$message.success(res.data.data.state)
            } else {
               this.$message.error(res.data.data)
            }
            this.formr = {
               mobile: '',
               password: '',
               step: ''
            }
         }


      }
   }
}
</script>

